.App {
  text-align: center;
}

.app-container {
  display: flex;
}

body,
#root {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.barchart-container {
  flex: 1 1;
}

.wrapper {
  position: relative; /* necessary to push footer to bottom */
  height: auto; /* necessary to push footer to bottom */
  min-height: 100%; /* fills the space when content is less than 100% height of viewport */
  width: 100%;
  margin: 0;
  padding-bottom: 80px; /* creates a space for footer to overlap .wrapper. MUST match footer properties height & margin-top (Footer.css) */
}

@media (min-width: 992px) {
  .wrapper {
    padding-bottom: 64px; /* creates a space for footer to overlap .wrapper */
  }
}

/*****************************************/
/****** COLORS ***************************/
/*****************************************/
.acc-primary {
  color: #00b5e0;
}
.acc-primary-bg {
  background: #00b5e0;
}
.acc-secondary {
  color: #8ec957;
}
.acc-secondary-bg {
  background: #8ec957;
}
.acc-admin {
  color: #eeb50e;
}
.acc-admin-bg {
  background: #eeb50e;
}

/*****************************************/
/****** BUTTONS **************************/
/*****************************************/

/* Rectangular Buttons */
.acc-btn {
  padding: 8px 20px;
  margin-right: 10px;
  margin-bottom: 10px;
  font-weight: bold;
  line-height: 1 !important;
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}
.acc-btn-primary {
  color: #fff;
  border: 1px solid #00b5e0;
  background: #00b5e0;
}
.acc-btn-primary:hover {
  color: #00b5e0;
  border: 1px solid #00b5e0;
  background: transparent;
}
.acc-btn-secondary {
  color: #fff;
  border: 1px solid #8ec957;
  background: #8ec957;
}
.acc-btn-secondary:hover {
  color: #8ec957;
  border: 1px solid #8ec957;
  background: transparent;
}
/* Oval Buttons */
.grad-btn {
  padding: 8px 15px;
  margin-right: 10px;
  margin-bottom: 10px;
  font-weight: bold;
  line-height: 1 !important;
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  -webkit-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
/* Primary */
.grad-btn-primary {
  color: #00b5e0;
  border: 1px solid #00b5e0;
  background: transparent;
}
.grad-btn-primary:hover {
  color: #fff;
  border: 1px solid #00b5e0;
  background: #00b5e0;
}
.grad-btn-primary:focus {
  color: #00b5e0;
  border: 1px solid #00b5e0;
  background: transparent;
}
/* Secondary */
.grad-btn-secondary {
  color: #8ec957;
  border: 1px solid #8ec957;
  background: transparent;
}
.grad-btn-secondary:hover {
  color: #fff;
  border: 1px solid #8ec957;
  background: #8ec957;
}
.grad-btn-secondary:focus {
  color: #8ec957;
  border: 1px solid #8ec957;
  background: transparent;
}
/* Admin */
.grad-btn-admin {
  color: #eeb50e;
  border: 1px solid #eeb50e;
  background: transparent;
}
.grad-btn-admin:hover {
  color: #fff;
  border: 1px solid #eeb50e;
  background: #eeb50e;
}
/* Admin Submit */
.grad-btn-admin-submit {
  color: #fff;
  border: 1px solid #eeb50e;
  background: #eeb50e;
  margin-top: 5px;
}
.grad-btn-admin-submit:hover {
  color: #eeb50e;
  border: 1px solid #eeb50e;
  background: transparent;
}
/* Admin isActive */
.grad-btn-admin-active {
  color: #eeb50e;
  border: 1px solid #eeb50e;
  background: #fff;
  width: 90px;
  padding-left: 0;
  padding-right: 0;
}
.grad-btn-admin-active:hover {
  color: #fff;
  border: 1px solid #adadad;
  background: #adadad;
}
.grad-btn-admin-active:focus {
  color: #eeb50e;
  border: 1px solid #eeb50e;
  background: #fff;
  outline: 5px auto #eeb50e;
  outline-offset: -2px;
}
.grad-btn-admin-inactive {
  color: #adadad;
  border: 1px solid #adadad;
  background: #fff;
  width: 83px;
  padding-left: 0;
  padding-right: 0;
}
.grad-btn-admin-inactive:hover {
  color: #fff;
  border: 1px solid #eeb50e;
  background: #eeb50e;
}
.grad-btn-admin-inactive:focus {
  color: #adadad;
  border: 1px solid #adadad;
  background: #fff;
  outline: 5px auto #adadad;
  outline-offset: -2px;
}
.grad-btn-admin-active:hover span,
.grad-btn-admin-inactive:hover span {
  display: none;
}
.grad-btn-admin-active:hover:after {
  content: "Deactivate";
}
.grad-btn-admin-inactive:hover:after {
  content: "Activate";
}

/* Add */
.grad-btn.add-btn {
  font-size: 22px;
  margin: 3px 12px 0;
  padding: 1px 6px;
}

@media (min-width: 480px) {
  .grad-btn.add-btn {
    font-size: 26px;
    margin-left: 12px;
    margin-top: 3px;
    padding: 1px 7px;
  }
}

/*****************************************/
/****** SEARCH RESULTS INFO **************/
/*****************************************/
.links {
  margin-right: 10px;
  padding: 8px 8px;
}
.links i {
  color: #00b5e0;
}
.links:hover i {
  color: #fff;
}
.skills {
  color: #00b5e0;
  font-size: 600;
  word-spacing: 2px;
}

/*****************************************/
/****** MISC BOOTSTRAP OVERRIDES *********/
/*****************************************/
.btn-primary {
  background: #00b5e0;
}
.btn-link {
  color: #00b5e0;
  font-weight: 500;
  text-align: left;
  padding: 0;
}
.btn-link:hover {
  color: #00b5e0;
}

.acc-navbar {
  background-color: #383838;
  border-radius: 0;
  margin-bottom: 0;
}
.acc-navbar ul.navbar-nav {
  margin: 0 -15px;
}
.acc-header {
  position: relative;
}
.acc-brand {
  padding: 0 15px;
  height: auto;
  line-height: 94px;
}
.acc-brand img {
  position: relative;
  display: inline-block;
  width: 225px;
}
.navbar-inverse .navbar-nav>li.acc-link > a {
  color: #fff;
  font-family: "Roboto Condensed", "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 900;
}
.navbar-inverse .navbar-nav>li.acc-link:hover a {
  color: #00b5e0;
}
.navbar-inverse .navbar-nav>.acc-link.active > a {
  color: #00b5e0;
  background-color: transparent;
}
.navbar-toggle {
  background-color: #00b5e0;
}
.navbar-inverse .navbar-toggle:focus, .navbar-inverse .navbar-toggle:hover {
  background-color: #00b5e0;
}
.nav>li.nav-search-word {
  display: none;
}
.nav>li.nav-search-icon {
  display: block;
}
@media (min-width: 480px) {
  .acc-brand img {
    width: 365px;
  }
}
@media (min-width: 768px) {
  .navbar-nav li a {
    line-height: 94px;
    padding: 0 15px;
  }
  .nav>li.nav-search-word {
    display: block;
  }
  .nav>li.nav-search-icon {
    display: none;
  }
}
@media only screen and (max-width: 767px) {
  .navbar-toggle {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -webkit-transform: translateY(-50%);
      -o-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      margin: 0 15px 0;
  }
  .nav>li.nav-search-icon {
    display: block;
    float: right;
  }
  .acc-header.non-admin {
    float: left;
  }
  .nav>li.nav-search-icon a {
    line-height: 91px;
    padding: 3px 15px 0;
  }
}

.clearfix {
  clear: both;
}
footer {
  margin-top: auto;
}
footer p,
footer a {
  color: #fff;
  font-family: "Roboto Condensed", "Roboto", sans-serif;
  font-size: 12px;
  font-weight: 900;
  margin-top: 22px;
}
footer p {
  display: block;
  float: none;
  clear: both;
  margin: 22px auto 0;
}
footer a {
  text-decoration: none;
  padding: 8px;
}
footer a:hover {
  text-decoration: none;
  color: #00b5e0;
}
footer li {
  float: left;
  list-style-type: none;
}
footer ul {
  display: inline-block;
  float: none;
  margin: 0 auto;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}
@media (min-width: 480px) {
  footer a {
    padding: 10px;
  }
}
@media (min-width: 992px) {
  footer {
    height: 64px; /* MUST match margin-top & .wrapper padding-bottom (App.css). */
    width: 100%;
    background: #000;
    text-align: center;
  }
  footer p,
  footer a {
    font-size: 14px;
  }
  footer ul {
    float: right;
    margin-top: 22px;
  }
  footer p {
    float: left;
  }
}

.login {
  max-width: 480px;
  margin: auto;
}
.login header {
  margin: 35px auto 20px;
  padding: 0;
}
.login-input {
  margin: 10px 0;
}
.login .form-group {
  margin-bottom: 0;
}
.login-btn {
  margin: 10px 0;
  width: 100%;
}
.login .panel {
  margin-top: 4em;
  margin-bottom: 4em;
  border: 1px solid #ccc;
}
.login-error {
  margin-bottom: 0;
}

/* --- Other Components -------*/
.grad-error {
  margin: 40px auto;
  max-width: 800px;
}
.card {
  width: 20rem;
  background: -webkit-radial-gradient(
    rgba(255, 255, 255, 1) 50%,
    rgba(216, 216, 216, 1) 165%
  );
  background: radial-gradient(
    rgba(255, 255, 255, 1) 50%,
    rgba(216, 216, 216, 1) 165%
  );
  margin-bottom: 2rem;
  border-radius: 5px;
}

.card:hover {
  box-shadow: 5px 10px 20px 1px rgba(0, 0, 0, 0.253) !important;
}

.card-body {
  padding: 1rem 0 !important;
}

.card-text {
  font-size: 4.5rem;
  padding: 0.4rem 0rem;
}

.container-fluid-row {
  padding-top: 6rem;
}

center-chart {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

html,
body {
  margin: 0;
}

#Barchart {
  font-family: sans-serif;

  /* Give app full page to work with */
  height: 100vh;
}

#page-wrap {
  text-align: center;

  /* Prevent sidebar from showing a scrollbar on page */
  overflow: auto;
}

/* Individual item */
.bm-item {
  display: inline-block;

  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
}

/* Change color on hover */
.bm-item:hover {
  color: white;
}

/* The rest copied directly from react-burger-menu docs */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 120px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

